import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Storage } from '@ionic/storage';
import { DomSanitizer } from '@angular/platform-browser';
import * as moment from 'moment';
import { DownloadService } from 'src/services/download.service';
import { LoaderService } from 'src/services/loader.service';
import { ToastService } from 'src/services/toast.service';
import {EventProviderService} from "../../services/event-provider.service";
import { CommonService } from 'src/services/common.service';
import { Platform } from '@ionic/angular';
import { File } from '@ionic-native/file/ngx';
import { FileTransfer } from '@ionic-native/file-transfer/ngx';
import { InAppBrowserOptions } from '@ionic-native/in-app-browser/ngx';
import { DocumentViewer } from '@ionic-native/document-viewer/ngx';
import { FileOpener } from '@ionic-native/file-opener/ngx';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
@Component({
  selector: 'app-downloads',
  templateUrl: './downloads.page.html',
  styleUrls: ['./downloads.page.scss'],
})
export class DownloadsPage implements OnInit {
  downloads: any[] = [];
  moment: any = moment;
  auth: any;
  isUserLoggedIn: boolean = false;
  account_donor_id: any;
  device_id: any;

  options: InAppBrowserOptions = {
    closebuttoncaption: "Close",
    closebuttoncolor: "#FFFFFF",
    disallowoverscroll: "no",
    hidenavigationbuttons: "no",
    toolbar: "yes",
    toolbarposition: "top",
    location: "yes",
  };






  constructor(
    private ft: FileTransfer,private file: File,private platform: Platform,private dowloadService: DownloadService, private dom: DomSanitizer, private loader: LoaderService,
    private location: Location,private storage: Storage,private toast : ToastService,private event_provider: EventProviderService,
    private documentviewer: DocumentViewer,private fileOpener: FileOpener,private iab: InAppBrowser,
    private common: CommonService,) { }

  ngOnInit() {
  }

  ionViewDidEnter(){
    this.getUser();
    this.loader.presentLoading().then(() => {
      // if(this.isUserLoggedIn && this.account_donor_id) {
        // api/Builder/churchs/favorite/device-fca134-e1ee3e-1a1c88-03c593-898db5/en: 
        this.dowloadService.getDownloads(this.device_id).subscribe(res => {
          if(res.status){
            this.downloads = res.favorites;
            this.downloads.forEach(item => {
              item['file_url'] = item['file_url'];
              // item['file_url'] = this.dom.bypassSecurityTrustResourceUrl(item['file_url']);
            })
          } else {
            this.toast.presentToastWithOptions('No download exist for this user' , 'Sorry' , 'warning')
          }
          this.loader.stopLoading();
        });
      // } else {
      //   this.loader.stopLoading();
      //   this.toast.presentToastWithOptions('User must be logged in' , 'Sorry' , 'warning')
      // }

    })
  }

  getUser() {
    // Getting User from local storage
    this.storage.get('user').then(res => {
      if (res != null && res != 'guest') {
        this.isUserLoggedIn = true;
        this.account_donor_id = res.user.id;
        this.auth = res.auth;
      }

    });
    this.device_id =localStorage.getItem('device_id');
  }

  bypassUrl(url){
    return this.dom.bypassSecurityTrustResourceUrl(url);
  }
  openPdf(newBypassLink){
    //this.loader.presentLoading();
    //console.log('clickedddddd', newBypassLink);
    if (this.platform.is('cordova')) {
      let path = this.file.dataDirectory;
      const transfer = this.ft.create();
      // console.log('trnasfer created: ', transfer);
      transfer.download(newBypassLink, `${path}myfile.pdf`).then((res: any)=>{
        //  console.log('response from download: ', res);
          let url2 = res.toURL();
          // console.log('to url is: ', url2);
          if(this.platform.is('ios' || 'iphone')){
              this.documentviewer.viewDocument(encodeURI(url2), 'application/pdf', {});
          }else if(this.platform.is('android')){
             // console.log('inside the esle: ');
              this.fileOpener.open(url2, 'application/pdf');
          }
      });
    }else{
     this.iab.create(newBypassLink, '_blank', this.options);
    }
  }

  gotobackPage() {
    this.location.back();
  }
  ionViewWillEnter() {
    this.ngOnInit();
  
    this.event_provider.getChurchData.subscribe(async (res: any) => {
        if (!(Object.keys(res).length === 0)) {
            // asking for password
            let app_pages = res.apppages.find(x => x.name === 'Download');
            if (app_pages.password_protection_toggle === 'true') {
                this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
            }
        }
    });
  
  }
}
